import React, { Component } from "react";

import GithubIcon from "./icons/github-original.svg";
import Linkedin from "./icons/linkedin-plain.svg";

class Footer extends Component {
  state = {};
  render() {
    return (
      <footer id="contact">
        <p>Contact me</p>
        <div className="icons-container">
          <span className="footer-icon">
            <a href="https://www.linkedin.com/in/shiven-nayee/">
              <img src={Linkedin} alt="Linkedin" />{" "}
            </a>
          </span>
          <span className="footer-icon">
            <a href="https://github.com/shiven-nayee">
              <img src={GithubIcon} alt="GithubIcon" />{" "}
            </a>
          </span>
        </div>
        <a href="mailto:shiven.nayee@gmail.com">Shiven.nayee@gmail.com</a>
      </footer>
    );
  }
}
export default Footer;



