// Components
import React, { Component } from 'react';

// CSS
import './css/App.css';

// Component Imports
import NavMenu from './components/NavMenu';
import Body from './components/Body';

class App extends Component {
  render() {
    return (
      <div className="App">
        <NavMenu />
        <Body />
      </div>
    );
  }
}

export default App;



