import React, { Component } from "react";

// Icon imports
import AWS from './icons/amazonwebservices-original-wordmark.svg';
import Bootstrap from './icons/bootstrap-plain-wordmark.svg';
import Chrome from './icons/chrome-original-wordmark.svg';
import CSS3 from './icons/css3-original-wordmark.svg';
import Docker from './icons/docker-original-wordmark.svg';
import Express from './icons/express-original-wordmark.svg';
import Gimp from './icons/gimp-original-wordmark.svg';
import Git from './icons/git-original-wordmark.svg';
import Github from './icons/github-original-wordmark.svg';
import Heroku from './icons/heroku-original-wordmark.svg';
import HTML5 from './icons/html5-original-wordmark.svg';
import intelliJ from './icons/intellij-original-wordmark.svg'
import java from './icons/java-original-wordmark.svg';
import javascript from './icons/javascript-original.svg';
import jQuery from './icons/jquery-original-wordmark.svg';
import linux from './icons/linux-original.svg';
import nodejs from './icons/nodejs-original-wordmark.svg';
import npm from './icons/npm-original-wordmark.svg';
import postgresql from './icons/postgresql-original-wordmark.svg';
import python from './icons/python-original-wordmark.svg';
import reactIcon from './icons/react-original-wordmark.svg';
import sass from './icons/sass-original.svg';
import ssh from './icons/ssh-original-wordmark.svg'
import VsCode from './icons/visualstudio-plain-wordmark.svg';
import yarn from './icons/yarn-original-wordmark.svg'

class Skills extends Component {
  state = {};
  render() {
    return (
      <div className="skills" id="skills-section">
        <h1>Skills</h1>
        <div className="skills-container">
        <span className="icon">< img src={HTML5} alt="HTML5"/> </span>
        <span className="icon">< img src={CSS3} alt="CSS"/> </span>
        <span className="icon">< img src={javascript} alt="Javascript"/> </span>
        <span className="icon">< img src={java} alt="Java"/> </span>
        <span className="icon">< img src={python} alt="Python"/> </span>
        <span className="icon">< img src={sass} alt="Sass"/> </span>
        <span className="icon">< img src={reactIcon} alt="React"/> </span>
        <span className="icon">< img src={jQuery} alt="jQuery"/> </span>
        <span className="icon">< img src={Bootstrap} alt="Bootstrap"/> </span>
        <span className="icon">< img src={intelliJ} alt="IntelliJ Idea"/> </span>
        <span className="icon">< img src={VsCode} alt="Visual Studio Code"/> </span>
        <span className="icon">< img src={postgresql} alt="Postgresql"/> </span>
        <span className="icon">< img src={AWS} alt="Amazon Web Services"/> </span>
        <span className="icon">< img src={Heroku} alt="Heroku"/> </span>
        <span className="icon">< img src={Docker} alt="Docker"/> </span>
        <span className="icon">< img src={nodejs} alt="Node.js"/> </span>
        <span className="icon">< img src={Express} alt="Express"/> </span>
        <span className="icon">< img src={Chrome} alt="Chrome"/> </span>
        <span className="icon">< img src={Gimp} alt="Gimp"/> </span>
        <span className="icon">< img src={linux} alt="Linux"/> </span>
        <span className="icon">< img src={Git} alt="Git"/> </span>
        <span className="icon">< img src={Github} alt="Github"/> </span>
        <span className="icon">< img src={ssh} alt="SSH"/> </span>
        <span className="icon">< img src={npm} alt="Node Package Manager"/> </span>
        <span className="icon">< img src={yarn} alt="Yarn"/> </span>
      </div>
      </div>
    );
  }
}

export default Skills;



