import React, { Component } from "react";

class Summary extends Component {
  state = {};
  render() {
    return (
      <div className="summary-container">
        <section className="summary">
          <p>
            I am an inquisitive Web Developer looking to join a dynamic, fun,
            and hardworking team dedicated to developing intelligent, functional
            solutions for businesses. As a freelance contractor, I have worked
            with a variety of software while specializing in Javascript and
            Front-end development.
          </p>
        </section>
      </div>
    );
  }
}

export default Summary;



