import React, { Component } from "react";

// Components
import PortfolioItem from "./PortfolioItem";

// Images
import TicTacToe from "./images/Tic Tac Toe.png";
import AlienCatInvasion from "./images/Alien Cat Invasion.png";
import CreateReactApp from "./images/Create React App.png";
import Wifinder from "./images/Wifinder.png";

// Icons
import GithubIcon from "./icons/github-original.svg";
import ChromeIcon from "./icons/chrome-plain.svg";
import InfoIcon from "./icons/info-circle.svg";

class Portfolio extends Component {
  constructor(props) {
    super(props);
    const GitIcon = <img src={GithubIcon} alt={"Github"} />;
    const Chrome = <img src={ChromeIcon} alt={"Chrome"} />;
    const Info = <img src={InfoIcon} alt={"Info Icon"} />;
    this.state = {
      popovers: [
        {
          placement: "top",
          text: <img src={TicTacToe} alt={"Tic Tac Toe game screenshot"} />,
          title: "Tic Tac Toe",
          body: (
            <div className="inside-popup">
              <p className="inner-p">
                {Chrome}{" "}
                <a
                  href="http://shivens-tic-tac-toe.bitballoon.com/"
                  target="_blank"
                >
                  Link to App
                </a>
                <br />
                {GitIcon}{" "}
                <a
                  href="https://github.com/shiven-nayee/Tic-Tac-Toe"
                  target="_blank"
                >
                  Source on Github
                </a>
                <br />
                {Info} A Tic Tac Toe game made using Html, CSS and javascript.
              </p>
            </div>
          )
        },
        {
          placement: "top",
          text: (
            <img
              src={AlienCatInvasion}
              alt={"Alien Cat Invasion game screenshot"}
            />
          ),
          title: "Alien Cat Invasion",
          body: (
            <div className="inside-popup">
              <p className="inner-p">
                {Chrome}{" "}
                <a
                  href="http://alien-space-cat-invaders.bitballoon.com/"
                  target="_blank"
                >
                  Link to App
                </a>
                <br />
                {GitIcon}{" "}
                <a
                  href="https://github.com/shiven-nayee/Alien-Space-Cat-Invasion-I"
                  target="_blank"
                >
                  Source on Github
                </a>
                <br />
                {Info} A Space Invaders style shooter game made using HTML, CSS, and Javascript.
              </p>
            </div>
          )
        },
        {
          placement: "top",
          text: <img src={CreateReactApp} alt="Custom C.R.A Boilerplate" />,
          title: "Custom C.R.A Boilerplate",
          body: (
            <div className="inside-popup">
              <p className="inner-p">
                {Chrome}{" "}
                <a href="https://www.npmjs.com/package/shivens-react-script" target="_blank">
                  Link to App
                </a>
                <br />
                {GitIcon}{" "}
                <a href="https://github.com/shiven-nayee/create-react-app" target="_blank">
                  Source on Github
                </a>
                <br />
                {Info} A custom boiler plate that generates a react project with Bootstrap, Sass and other scripts.
              </p>
            </div>
          )
        },
        {
          placement: "top",
          text: (
            <img src={Wifinder} alt={"NYC hotspot finder App screenshot"} />
          ),
          title: "Wifinder",
          body: (
            <div className="inside-popup">
              <p className="inner-p">
                {Chrome}{" "}
                <a href="http://hotspot-finder.s3-website-us-east-1.amazonaws.com/" target="_blank">
                  Link to App
                </a>
                <br />
                {GitIcon}{" "}
                <a href="https://github.com/shiven-nayee/hotspot-finder" target="_blank">
                  Source on Github
                </a>
                <br />
                {Info} A full stack application that finds free wifi hotspots built using React, Bootstrap, and Spring for the backend.
              </p>
            </div>
          )
        }
      ]
    };
  }

  render() {
    return (
      <div className="portfolio-container" id="portfolio">
        {this.state.popovers.map((popover, i) => {
          return <PortfolioItem key={i} item={popover} id={i} />;
        })}
      </div>
    );
  }
}

export default Portfolio;



